.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 150px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Nav-list {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 0px;
  margin: 0px;
  background-color: greenyellow;
  width: 100vw;
  min-height: 100px;
}

#linkBtn {
  padding: 20px;
}
.Nav-list li {
  list-style-type: none;
  padding: 20px 40px;
  background-color: pink;
}

.Nav-list li  a {
  text-decoration: none;
}

.Main-page {
  display: flex;
  align-items: center;
  justify-items: center;
  flex-direction: column;
  background-color: #282c34;
  color: white;
  min-height: 70vh;
}

.Page {
  display: flex;
  align-items: center;
  justify-items: center;
  flex-direction: column;
  background-color: #282c34;
  color: white;
  min-height: 70vh;
  /* flex-wrap: wrap; */
}
p {
  max-width: 80vw;
}
.BiggestH {
  font-size: 10em;
}

footer {
  height: 10vh;
  min-height: 100px;
  background-color: #282c34cc;

  display: flex;
  margin-top: auto;
  flex-direction: row;
  
  justify-content: center;
  align-items: center;


  /* height: 5vh;
  min-height: 40px;

  display: flex;
  margin-top: auto;

  justify-content: center;
  align-items: center; */
}

#root {
  display: flex;
  flex-direction: column;
  align-content: stretch;
  height: 100vh;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
